.Alert {
  border-radius: 4px;
  padding: 16px 24px 16px 8px;
  font-size: 1em;
  border-width: 0 0 0 5px;
  border-style: solid;
  letter-spacing: 0.5px;
  display: flex;
}

.AlertIcon {
  padding: 0 8px;
}

.AlertBox {
  padding-left: 8px;
}

.AlertTitle {
  padding: 2px 0;
  display: inline-block;
}

.AlertMessage {
  margin: 0;
}