<template>
	<span>
		<span v-if="loading">
			<q-icon
				name="mdi-spin mdi-loading"
				:color="fontColor || 'primary'"
				:size="size || 'sm'"
				v-bind="$attrs"
				/>
		</span>
		<span v-else-if="!loading && icon.name.indexOf('img:') > -1">
			<q-icon
				:name="icon.name"
				:color="fontColor"
				:size="size || 'sm'"
				:style="style"
				/>
			
		</span>
		<span v-else-if="!loading && icon.name.indexOf('img:') === -1">
			<span
				:data-icon="icon.name"
				:style="style"
				:color="fontColor"
				class="iconify"
				:class="{
					['icon-' + (size || 'sm')]: fontSize === false,
					'flip-horizontal': icon.attributes.flip === 'h',
					'flip-vertical': icon.attributes.flip === 'v',
					['rotate-' + icon.attributes.rotate]: icon.attributes.rotate !== undefined,
					'blink': icon.attributes.blink,
					'spin': icon.attributes.spin
				}"
				/>
			
		</span>
	</span>
</template>

<script lang="ts">
import { defineComponent } from '@vue/composition-api';

import { colors } from 'quasar';

import { icon as getIcon } from 'src/helpers/utils';
import Iconify from '@iconify/iconify'; // eslint-disable-line no-unused-vars

export default defineComponent({
	'name': 'Icon',
	'props': [ 'name', 'loading', 'color', 'size', 'vAlign' ],
	
	data() {
		return {
			'style': ''
		}
	},
	
	mounted() {
		this.style = 'vertical-align: ' + (this.vAlign || 'text-bottom') + (this.$el.getAttribute('style') ? ', ' + this.$el.getAttribute('style') : '');
	},
	
	'computed': {
		icon() {
			return getIcon(this.name, true);
		},
		fontColor() {
			return this.style.indexOf('color') > -1 ? undefined : ([ 'primary', 'secondary', 'accent', 'positive', 'warning', 'negative', 'info' ].includes(this.color) ? colors.getBrand(this.color) : (this.color || ''));
		},
		fontSize() {
			return this.style.indexOf('font-size') > -1;
		}
	}
});
</script>

<style>
.icon-xs {
	font-size: 18px;
}

.icon-sm {
	font-size: 24px;
}

.icon-md {
	font-size: 32px;
}

.icon-lg {
	font-size: 38px;
}

.icon-xl {
	font-size: 46px;
}
</style>
