.cover {
  background-color: #1976D2;
  background-repeat: no-repeat;
  background-position: center;
}

.track-information {
  height: 50px;
  background-color: #1976D2;
  color: #fff;
}

.track-duration {
  width: 100%;
  background-color: #26A69A;
  cursor: pointer;
}

.track-progress {
  height: 6px;
  background-color: #9C27B0;
  cursor: pointer;
}

.controls {
  height: 50px;
  display: flex;
  justify-content: space-between;
}