















































































































































































































.mapJumper {
	color: #666;
	position: absolute;
	left: 0;
	bottom: 0;
	width: 100%;
	height: 60px;
	z-index: 999;
	display: flex;
	align-items: center;
	margin-left: 16px;
}

.jumper {
	cursor: pointer;
}

.mapMarker {
	font-size: 2rem;
	background-color: #fff;
	color: var(--q-color-primary);
	height: 48px !important;
	width: 48px !important;
	/*opacity: 0.8;*/
	text-align: center;
	border-radius: 50%;
	margin: 0 8px;
}

.mapMarkerCaption {
	text-align: center;
	font-size: 11px;
    overflow: hidden;
    width: 48px;
    text-overflow: ellipsis;
    white-space: nowrap;
	margin: 0 8px;
}
