<template>
	<div
		class="q-pa-sm HomeKitTile grid"
		:class="{ 'cursor-pointer': config.jumper !== '' }"
		:style="{
			'height': containerSize.height + 'px',
			'width': containerSize.width + 'px'
		}"
		@click="jump"
		>
		
		<q-linear-progress v-show="!loaded && !error" indeterminate />
		<alert v-show="loaded && error">{{ error }}</alert>
		
		<div class="full-height" v-show="loaded && !error">
			<div v-for="v in vertical" :key="v" :class="v" class="item row">
				<div v-for="h in horizontal" :key="h" :class="h" class="col-grow">
					<!-- ICON -->
					<icon v-if="config[v + capitalize(h)] === 'icon'" :style="device.getStyle('icon', deviceConfig.primaryStateKey, primaryStateKey.val)" :loading="!loaded" :name="deviceIcon" color="primary" />
					
					<!-- LABEL -->
					<div v-if="config[v + capitalize(h)] === 'label'">{{ deviceConfig.label || device.label || device.name }}</div>
					
					<!-- State -->
					<component v-if="config[v + capitalize(h)] === 'state' && deviceConfig.primaryStateKey" :is="FUNCTIONS_COMPONENTS.components.State" :device="device" :stateKey="deviceConfig.primaryStateKey" :config="deviceConfig" />
					
					<!-- Body Element -->
					<component v-if="config[v + capitalize(h)] === 'bodyElement' && deviceConfig.bodyStateKey && deviceConfig.bodyElement" :is="getComponent(device.function, 'body', deviceConfig.bodyElement)" :device="device" :stateKey="deviceConfig.bodyStateKey" :config="deviceConfig" />
					
					<!-- Action Element -->
					<component v-if="config[v + capitalize(h)] === 'actionElement' && deviceConfig.primaryStateKey && deviceConfig.actionElement" :is="getComponent(device.function, 'action', deviceConfig.actionElement)" :device="device" :stateKey="deviceConfig.primaryStateKey" :config="deviceConfig" />
				</div>
			</div>
		</div>
	</div>
</template>

<script lang="ts">
import { defineComponent } from '@vue/composition-api';

import { format } from 'quasar';
const { capitalize } = format;

import { joinConfig } from 'src/modules';
import ConfigFile from './HomeKitTile.config';

import { allSettled } from 'src/helpers/utils';
import Devices from 'src/controllers/Devices';
import { components as FUNCTIONS_COMPONENTS } from 'src/functions';

export default defineComponent({
	'name': 'HomeKitTile',
	'props': [ '_containerSize', 'widget' ],
	
	created() {
		this.deviceConfig = this.widget.items[0] || {};
		this.device = Devices.get(this.deviceConfig.deviceId || null);
		
		if (this.device) {
			allSettled([
				this.device.getState(this.deviceConfig.primaryStateKey, state => { this.primaryStateKey = state }),
				this.device.getState(this.deviceConfig.bodyStateKey, state => { this.bodyStateKey = state })
			])
				.then(() => {
					this.error = false;
					this.loaded = true;
				});
		}
		else {
			this.error = this.$i18n.t('Device {deviceId} not found').replace('{deviceId}', this.deviceConfig.deviceId);
			this.loaded = true;
		}
	},
	
	data() {
		this.FUNCTIONS_COMPONENTS = FUNCTIONS_COMPONENTS;
		this.capitalize = capitalize;
		this.horizontal = [ 'left', 'center', 'right' ];
		this.vertical = [ 'top', 'middle', 'bottom' ];

		return {
			'containerSize': this._containerSize,
			
			'bodyStateKey': {},
			'primaryStateKey': {},
			'loaded': false,
			'error': false
		}
	},
	
	'watch': {
		_containerSize(_containerSize) {
			this.containerSize = _containerSize;
		}
	},
	
	'computed': {
		config() {
			return joinConfig(this.widget.config, ConfigFile);
		},
		deviceIcon() {
			return this.device && this.device.getIcon(this.deviceConfig.primaryStateKey, this.primaryStateKey.val);
		}
	},
	
	'methods': {
		getComponent(deviceFunction, key = 'action', component = 'State') {
			return (FUNCTIONS_COMPONENTS[deviceFunction] && FUNCTIONS_COMPONENTS[deviceFunction][key][component]) || (FUNCTIONS_COMPONENTS._defaults && FUNCTIONS_COMPONENTS._defaults[key][component]) || null;
		},
		jump() {
			// URL
			if (this.config.jumper.indexOf('http') > -1 || this.config.jumper.indexOf('www') > -1) {
				window.open((this.config.jumper.indexOf('http') === -1 ? 'http://' : '') + this.config.jumper, '_blank');
			}
			
			// Tab ID
			else {
				this.$router.push({ 'params': { 'tabId': this.config.jumper } }).catch(error => console.warn(error));
			}
		}
	}
});
</script>

<style scoped>
.HomeKitTile.grid {
	overflow: hidden;
}

.HomeKitTile.grid .item {
	height: 33.33%
}

.HomeKitTile.grid .top > div {
}

.HomeKitTile.grid .middle > div {
	margin-top: auto;
	margin-bottom: auto;
}

.HomeKitTile.grid .bottom > div {
	margin-top: auto;
	margin-bottom: 0;
}

.HomeKitTile.grid .left {
}

.HomeKitTile.grid .center {
	margin-left: auto;
	margin-right: auto;
	text-align: center;
}

.HomeKitTile.grid .right {
	margin-left: 0;
	margin-right: auto;
	text-align: right;
}

</style>
