<template>
	<div class="jarvis-StateHTML StateHTML">
		<q-linear-progress v-show="!loaded && !error" indeterminate />
		<alert v-show="loaded && error">{{ error }}</alert>
		
		<div v-show="loaded && !error">
			<div v-if="!hasScript" v-html="html" />
			<iframe
				v-else
				class="StateHTML-iFrame"
				:style="{ 'border': 0, 'height': size.height + 'px' }"
				:srcdoc="html"
				/>
		</div>
	</div>
</template>

<script lang="ts">
import { defineComponent } from '@vue/composition-api';

import { joinConfig } from 'src/modules';
import ConfigFile from './StateHTML.config';

import Devices from 'src/controllers/Devices';

export default defineComponent({
	'name': 'StateHTML',
	'props': [ 'widget', '_containerSize' ],
	
	created() {
		const item = this.widget.items[0] || {};
		this.device = Devices.get(item.deviceId || null);
		
		if (this.device) {
			this.device.getState(item.primaryStateKey, state => { this.state = state })
				.catch(error => { this.error = this.$i18n.t(error.message) })
				.finally(() => { this.loaded = true });
		}
		else {
			this.error = this.$i18n.t('No device specified');
			this.loaded = true;
		}
	},
	
	data() {
		return {
			'size': this._containerSize,
			
			'state': {},
			'loaded': false,
			'error': false
		}
	},
	
	'watch': {
		_containerSize(size) {
			this.size = size;
		}
	},
	
	'computed': {
		config() {
			return joinConfig(this.widget.config, ConfigFile);
		},
		html() {
			return this.state.val || '';
		},
		hasScript() {
			return this.html && this.html.indexOf('<script') > -1;
		}
	}
});
</script>

<style scoped>
.StateHTML-iFrame {
	width: 100%;
	border: 0;
}
</style>
