@import '~src/css/quasar.variables.sass', 'quasar/src/css/variables.sass'

code
	font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace

html, body, #q-app
	height: 100%

h6
	margin: 0

// DRAWER
.jarvis-drawer-pages .q-drawer
	//z-index: 99 !important
	bottom: -1px
	overflow: hidden

// DAWER MINI MODE FULLSCREEN ON THE SITE
.jarvis-drawer-pages.jarvis-drawer-mini-mode .q-drawer
	bottom: 0 !important
	top: 0 !important



// TABS
.jarvis-tabs .q-tabs__arrow
	display: none

.jarvis-tabs .q-tab__content
	min-width: 40px !important

.jarvis-tabs .tabIcon
	width: 24px
	height: 24px
	margin: 4px 8px 0 0

// GENERAL
.primary
	color: var(--q-color-primary)

.secondary
	color: var(--q-color-secondary)

.hCentered
	text-align: center

.wrap
	flex-wrap: wrap !important

.nowrap
	flex-wrap: nowrap !important
	white-space: nowrap

.overflow-dots
	overflow: hidden
	text-overflow: ellipsis

.notDisabled, .notDisabled *
	cursor: pointer !important

.q-toolbar
	min-height: 40px


// INPUTS
.q-field__control-container
	overflow: hidden


// DRAG N DROP
.dragndrop
	cursor: move
	z-index: 101

.flip-list-move
	transition: transform 0.5s

.no-move
	transition: transform 0s

.sortable-ghost
	opacity: 0.5
	background: #c8ebfb

.restore, .mdi-restore
	cursor: pointer !important

.q-item__label
	overflow: hidden
	text-overflow: ellipsis


// DELETED
.body--dark .rowDeleted
	background-color: $red-10 !important
	color: $red-1 !important

.rowDeleted
	background-color: $red-1 !important
	color: $red-10 !important

.itemDeleted
	text-decoration: line-through


// INPUTS
.inputChanged .q-field__label
	font-style: italic

.inputChanged .q-field__label
	font-style: italic
	color: var(--q-color-accent)

.inputChanged .q-field__control::before
	border-color: var(--q-color-accent)

.jsoneditor-type-modes.jsoneditor-selected
	background-color: var(--q-color-primary) !important


// BODY
.body
	background-color: $grey-1

.body--dark .body
	background-color: $grey-10

.body--light .bgcolor
	background-color: #fff

.body--dark .bgcolor
	background-color: var(--q-color-dark)


// PAPER / TILE
.paper
	@extend .bgcolor
	border-radius: $generic-border-radius
	box-shadow: $shadow-1
	margin: 8px

.body--dark .tile
	border-bottom: 2px solid $grey-10
	border-right: 2px solid $grey-10

.body--light .tile
	border-bottom: 2px solid $grey-1
	border-right: 2px solid $grey-1


// OTHER
.text-h7
	font-size: 1.1rem
	font-weight: 500
	line-height: 2rem
	letter-spacing: 0.0125em

.text-header
	padding: 24px 4px 4px 4px


// WHITE MODE
.q-layout
	background-color: $grey-2

.jarvis-tabs
	background-color: var(--q-color-primary) !important


// DARK MODE
.body--dark .jarvis-tabs
	background-color: $grey-9 !important

.body--dark .q-header
	background-color: var(--q-color-dark)

.body--dark .q-layout
	background-color: $grey-10

.body--light .q-footer
	background-color: #fff

.body--dark .q-footer
	background-color: $grey-10


// ANIMATIONS
.blink
	animation: blinker 1s linear infinite

@keyframes blinker
	50%
		opacity: 0

.spin
	animation: spinner 2s linear infinite

@keyframes spinner
	0%
		transform: rotate(0deg)
	100%
		transform: rotate(359deg)
