<template>
	<div> <!-- <div class="full-height"> -->
		<q-dialog
			v-model="jsonEditor"
			persistent
			>
			
			<q-card style="width: 700px; max-width: 80vw;">
				<q-card-section class="row items-center">
					<div class="text-h6">{{ $t('JSON Editor') }}</div>
					
					<q-space />
					<q-btn flat round color="grey" icon="close" @click="onJsonEditor(false, true)" />
				</q-card-section>

				<q-card-section class="body q-pa-none">
					<json-editor :json="jsonVal" @onChange="onJsonChange" />
				</q-card-section>

				<q-card-actions align="right">
					<q-btn flat :label="$t('Cancel')" color="negative" @click="onJsonEditor(false, true)" />
					<q-btn flat :label="$t('Apply')" color="primary" @click="onJsonEditor(false)" :disable="jsonError" />
				</q-card-actions>
			</q-card>
		</q-dialog>
		
		<q-input
			:type="(type === 'color' ? 'text' : type) || 'text'"
			stack-label
			:value="val"
			@input="onChange"
			:label="inputLabel"
			:placeholder="$t(placeholder)"
			:class="{ 'inputChanged': this.inputChanged, 'full-height': fullHeight !== undefined }"
			:error="error !== null"
			:error-message="error"
			:dense="dense !== undefined && dense !== 'bottom'"
			v-bind="$attrs"
			v-on="$listeners"
			style="margin: 0 8px"
			debounce="750"
			:hide-bottom-space="dense !== undefined || dense === 'bottom'"
			>
			
			<template v-slot:prepend v-if="type === 'color' || icon">
				<div v-if="type === 'color'">
					<icon size="xs" name="mdi-circle-slice-8" :style="{ color: val }" />
				</div>
				<icon size="xs" :name="icon" v-if="icon" />
			</template>
			
			<template v-slot:append>
				<q-icon name="colorize" class="cursor-pointer" v-if="type === 'color'">
					<q-popup-proxy transition-show="scale" transition-hide="scale">
						<q-color @change="onChange" :value="val" />
					</q-popup-proxy>
				</q-icon>
				
				<span v-if="$t(info)">
					<icon v-if="!link" size="xs" name="mdi-information" color="primary" vAlign="middle" />
					<q-btn v-if="link" size="sm" type="a" :href="link" target="_blank" round dense flat icon="mdi-information" color="primary" />
					<q-tooltip content-class="bg-grey-8" :offset="[4, 4]">{{ $t(info) }}</q-tooltip>
				</span>
				
				<q-btn v-if="json !== undefined" round dense flat icon="mdi-code-json" color="secondary" @click="onJsonEditor(true)" />
				<slot name="end"></slot>
			</template>
			<slot v-for="(_, name) in $slots" :name="name" :slot="name" />
			
		</q-input>
	</div>
</template>

<script lang="ts">
import { defineComponent } from '@vue/composition-api'

import JsonEditor from 'src/components/json-editor.vue';

export default defineComponent({
	'name': 'InputText',
	'props': [
		'full-height',
		'required',
		'id',
		'type',
		'label',
		'icon',
		'placeholder',
		'value',
		'info',
		'link',
		'json',
		'dense',
		'autofocus'
	],
	
	'components': {
		JsonEditor
	},
	
	'methods': {
		set(val) {
			this.error = null;
			this.jsonVal = val;
			
			if (this.val !== val) {
				this.val = val;
				this.$emit('onChange', { ...this.$attrs, ...this.$props, 'value': val });
			}
		},
		onChange(val) {
			// validation
			if (this.required !== undefined && !val) {
				this.error = this.$i18n.t('No input given');
				this.$emit('onChange', { ...this.$attrs, ...this.$props, '_error': this.error });
			}
			
			// verify json (do not verify on plain text when allowTextInput is set)
			else if (this.json !== undefined && val && !(this.json === 'allowTextInput' && val.indexOf('{') === -1 && val.indexOf('}') === -1 && val.indexOf('"') === -1)) {
				try {
					// remove quotes (") when no brackets found
					if (val.indexOf('{') === -1 && val.indexOf('}') === -1) {
						val = val.replace(/"/g, '');
					}
					else {
						const json = JSON.parse(val);
						if (json) {
							//console.debug(json);
						}
					}
					
					this.set(val);
				}
				catch (err) {
					this.val = val;
					
					this.error = this.$i18n.t('Wrong JSON format');
					this.$emit('onChange', { ...this.$attrs, ...this.$props, '_error': this.error });
				}
			}
			
			// no json
			else {
				this.set(val);
			}
		},
		onJsonChange({ _error, value }) {
			this.jsonError = _error !== undefined;
			
			if (_error === undefined) {
				this.jsonValModified = value;
			}
		},
		onJsonEditor(open, cancel = false) {
			// cancel
			if (cancel) {
				this.val = this.backupVal;
			}
			
			// save
			else if (cancel === false && open === false) {
				this.onChange(this.jsonValModified);
			}
			
			this.jsonValModified = this.jsonVal;
			this.backupVal = this.val;
			this.jsonEditor = open;
		},
		parseJson(val) {
			if (this.json !== undefined && val) {
				try {
					const json = typeof val === 'string' ? val : JSON.stringify(val);
					return json;
				}
				catch (err) {
					console.warn(val, err);
				}
			}
			
			return val;
		}
	},
	
	'computed': {
		inputChanged() {
			const val = this.val;
			return this.initialValue && this.initialValue.raw !== undefined ? (val !== this.initialValue.raw) : (this.initialValue !== undefined ? (val !== this.initialValue) : false);
		},
		inputLabel() {
			return this.$t(this.label) +
						(this.required !== undefined ? ' *' : '') +
						(this.json !== undefined ? ' (' + this.$t('JSON Format') + ')' : '') +
						(this.inputChanged ? ' (' + this.$t('modified') + ')' : '');
		}
	},
	
	data() {
		this.initialValue = this.json !== undefined ? this.parseJson(this.value) : this.value;
		this.jsonVal = {};
		this.jsonValModified = {};
		
		return {
			'val': this.parseJson(this.value && this.value.val !== undefined ? this.value.val : (this.value !== undefined ? this.value : '')),
			'error': null,
			'jsonEditor': false,
			'jsonError': false,
			'backupVal': ''
		}
	},
	
	created() {
		this.onChange(this.val);
	},
	
	'watch': {
		value(value) {
			const val = value && this.parseJson(value.val !== undefined ? value.val : value);
			
			if (val !== undefined && val !== this.val) {
				this.val = val;
			}
		}
	}
})
</script>

<style>
.q-field__control {
	height: 100% !important;
}
</style>
